/**
 * Name: BizAgi Desktop Widget Administration Display Options
 * 
 * @author Diego Armando Poveda
 */


bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.admin.displayOptions", {}, {
    
    init: function (workportalFacade, dataService, params) {
        var self = this;
        self.notifier = bizagi.injector.get("notifier");self.notifier = bizagi.injector.get("notifier");

        // Call base
        self._super(workportalFacade, dataService, params);

        //Load templates
        self.loadTemplates({
            "admin.displayOptions.container": bizagi.getTemplate("bizagi.workportal.desktop.widgets.admin.displayOptions").concat("#ui-bizagi-workportal-widget-admin-display-options-container")
        });
    },
    /*
    *   Returns the widget name
    */
    getWidgetName: function () {
        return bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_ADMIN_PREFERENCES;
    },
    
    renderContent: function () {
        var self = this;
        var content;
        content = self.content = $('<div></div>');
        self.loadtemplates();
        return content;
    },

    loadtemplates: function () {
        var self = this;

        self.containerTemplate = self.getTemplate("admin.displayOptions.container").markup;
    },

    postRender: function () {
        var self = this;
        var mainTemplate = self.containerTemplate;
        self.displayOptionsInstance = new Vue({
            template: mainTemplate,
            data: {
                maximizeModals: {
                    label: '',
                    value: false,
                    hover: false
                },
                startPage: {
                    label: '',
                    input: '-------------',
                    selected: '',
                    focus: false,
                    values: [
                        {value: '', displayName: 'render-combo-empty-value'},
                        {value: '1', displayName: 'workportal-widget-admin-displayoptions-startpage-auto'},
                        {value: '2', displayName: 'workportal-widget-admin-displayoptions-startpage-me'},
                        {value: '3', displayName: 'workportal-widget-admin-displayoptions-startpage-inbox'}
                    ]
                },
                defaultTitle: '',
                updatetTitle: '',
                OpenWindowsMaximized: false,
                showStartPageControl: false
            },
            computed: {
                activeIndex: function(){
                    return this.findIndex(this.startPage.input, this.startPage);
                }
            },
            methods: {
                getResource: function(resourceKey){
                    return window.bizagi.localization.getResource(resourceKey);
                },
                setHover: function (value) {
                    this.maximizeModals.hover = value;
                },
                setFocus: function (value) {
                    this.startPage.focus = value;
                },
                chooseValue: function (item) {
                    this.startPage.input = item.displayName;
                    this.startPage.selected = item.value;
                    this.setFocus(false);
                },
                debounce: bizagi.util.debounce(function(func, arg){
                    func.apply(this, [arg]);
                },200),
                keyup: function(e){
                    switch (e.keyCode) {
                        case 13: // enter
                            this.chooseEnter();
                            break;
                        case 38: // up
                        case 40: // down
                            this.changeOffset(e.keyCode - 39);
                        default:
                            this.setFocus(true);
                            break;
                    }
                },
                chooseEnter: function () {
                    var index = this.activeIndex;
                    var result = this.startPage.values[index];
                    this.startPage.input = result.displayName;
                    this.startPage.selected = result.value;
                    this.startPage.focus = false;
                },
                changeOffset: function (delta) {
                    var index = this.activeIndex;
                    var input = '';
                    index = index + delta;
                    if (index > -1 && index < this.startPage.values.length) {
                        this.startPage.input = this.startPage.values[index].displayName;
                    }
                },
                findIndex: function (input) {
                    var result, index = -1;
                    result = this.findMatches(input);
                    if (result) {
                        index = this.startPage.values.findIndex(function (v) {
                            return v.value === result.value;
                        });
                    }
                    return index;
                },
                findMatches: function (input) {
                    var element = this.startPage.values.find(function (v) {
                        return v.displayName.toLowerCase().indexOf(input.toLowerCase()) > -1;
                    });
                    return element;
                },
                defaultConfigurations: function () {
                    this.startPage.selected = '';
                    this.startPage.input = '-------------';
                    this.maximizeModals.value = false;
                },
                getRowValueCookie: function() {
                    // Get Cookie Value
                    var storedValue = '';
                    if(bizagi.cookie("bizagiUserDisplayOptions")){
                        storedValue = JSON.parse(bizagi.cookie("bizagiUserDisplayOptions"));
                    }
                    return storedValue;   
                },
                updateConfigurations: function () {
                    var rows = '10';
                    if(this.getRowValueCookie()){
                        if(this.getRowValueCookie().numberRows){
                            rows = this.getRowValueCookie().numberRows; //if there is a number row stored, it has to be keep in the storage.
                        }
                    }
                    var result = {startPage:'', maximizeModals: '', numberRows: rows};

                    if(this.showStartPageControl){
                        if (this.startPage.selected !== '') {
                            result.startPage = this.startPage.selected;
                        }
                    }else{
                        result.startPage = '';
                    }
                
                    
                    if(this.OpenWindowsMaximized){
                        result.maximizeModals = this.maximizeModals.value ? 'yes' : 'no';
                    }else{
                        result.maximizeModals = 'no';
                    }
                    
                    // Set Cookie
                    bizagi.cookie("bizagiUserDisplayOptions", JSON.stringify(result));
                    self.notifier.showSucessMessage(bizagi.localization.getResource("workportal-widget-admin-displayoptions-success"), bizagi.localization.getResource("workportal-menu-display-options"),"");
                }
            },
            // Components
            components: {
            },
            // Life Cycle Hooks
            created: function (){
                var that = this;
                this.maximizeModals.label =  this.getResource('workportal-widget-admin-displayoptions-maxmodals');
                this.startPage.label = this.getResource('workportal-widget-admin-displayoptions-startpage');
                this.defaultTitle = this.getResource('workportal-widget-admin-displayoptions-default');
                this.updatetTitle = this.getResource('workportal-widget-admin-displayoptions-update');
                this.startPage.values.forEach($.proxy(function(e) {
                    e.displayName = this.getResource(e.displayName);
                }, that));
                                
                // Load Cookie values 
                var storedValue = '';
                if(bizagi.cookie("bizagiUserDisplayOptions")){
                    storedValue = JSON.parse(bizagi.cookie("bizagiUserDisplayOptions"));
                    var initElement = this.startPage.values.find(function (v) {
                        return v.value === storedValue.startPage;
                    });
                    this.startPage.selected = initElement.value;
                    this.startPage.input = initElement.displayName;

                    this.maximizeModals.value = (storedValue.maximizeModals === 'yes') ? true : false;
                }
            },
            mounted: function (){
            },
            destroyed: function () {
            }
        });

        self.renderingDeferred.then($.proxy(self.mountView, self));
    },

    // Component Mount
    mountView: function() {
    	var self = this;
        var $content = self.getContent();

        self.security = new bizagi.workportal.command.security(self.dataService);
        $.when(self.security.getSecurity())
            .done(function(security) {
                // Checking for MaximizeModalsOption into security(response)
                if(bizagi.override.enableMaximizeModalsOption === false){
                    security.OpenWindowsMaximized = false;
                }
                
                self.displayOptionsInstance.OpenWindowsMaximized =  security.OpenWindowsMaximized;
                self.displayOptionsInstance.showStartPageControl =  bizagi.override.enableStartPageDisplayOption;

                setTimeout(function () {
                    self.displayOptionsInstance.$mount($content[0]);
                }, 100);
            });
    },

    // Component Destroy
	dispose: function () {
        var self = this;
        self._super();
        self.displayOptionsInstance.$destroy();
    }
});
